.nav-slider {
  position: relative;
}
.nav-slider__prev, .nav-slider__next {
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  background: #f3f3f3;
  border-radius: 50%;
  border: 0;
  position: absolute;
  z-index: 10;
  top: 50%;
  padding: 0.5em;
  font-size: 20px;
  cursor: pointer;
}
.nav-slider__prev {
  left: 0;
}
.nav-slider__next {
  right: 0;
}
.nav-slider__icon {
  font-size: 30px;
}
.nav-slider__text {
  margin-top: 10px;
  max-width: 5em;
}
.nav-slider .swiper-wrapper {
  display: flex;
}
.nav-slider .swiper-slide {
  flex: 0 0 auto;
  margin-right: 35px;
  text-align: center;
  width: auto;
  text-decoration: none;
  color: #000;
}
.nav-slider .swiper-slide:last-child {
  margin-right: 0;
}
.nav-slider .swiper-slide.active {
  color: #2eb674;
}

.audio-list {
  -moz-column-count: 2;
       column-count: 2;
  -moz-column-width: 50%;
       column-width: 50%;
  -moz-column-gap: 30px;
       column-gap: 30px;
  counter-reset: item;
}
.audio-list__index {
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2em;
  height: 2em;
  border-radius: 0.5em;
  background: #f3f3f3;
  color: #000;
}
.audio-list__index:before {
  content: counter(item);
}
.audio-list__item {
  counter-increment: item;
  display: flex;
  align-items: center;
  padding-bottom: 30px;
}
.audio-list__item.active .audio-list__icon-play {
  display: none;
}
.audio-list__item.active .audio-list__icon-pause {
  display: block;
}
.audio-list__button {
  position: relative;
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(8vw, 7em);
  height: min(8vw, 7em);
  background: #f3f3f3;
  background-size: cover;
  border-radius: 10px;
  border: 0;
  margin-left: min(2vw, 30px);
  overflow: hidden;
  color: #fff;
}
.audio-list__button:before {
  content: " ";
  background: #000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  cursor: pointer;
}
.audio-list__icon-play, .audio-list__icon-pause {
  position: relative;
  font-size: 2.5em;
}
.audio-list__icon-pause {
  display: none;
}
.audio-list__text {
  margin-left: min(2vw, 30px);
  margin-right: auto;
}
.audio-list__time {
  margin-left: min(2vw, 30px);
}
.audio-list__name {
  text-transform: uppercase;
}
.audio-list__desc {
  margin-top: 10px;
}

@media (max-width: 767.98px) {
  .audio-list {
    -moz-column-count: 1;
         column-count: 1;
  }
}